.slider {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  top: 0px;
  right: 0;
  left: 0px;
  bottom: 0;
  opacity: 0;
  animation: ani 1.5s forwards;

  @keyframes ani {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

  &-close {
    display: flex;
    position: absolute;
    top: 20px;
    right: 30px;
    width: 30px;
    height: 30px;
    background-image: url(../../../public/img/cross.svg);
    background-size: cover;
    cursor: pointer;
  }

  &-container {
    display: block;
    width: 700px;
    height: 700px;
    overflow: hidden;
    display: flex;
  }

  &-window {
    width: 100%;
    display: flex;
  }

  img {
    width: 100%;
    display: flex;
    margin: 0px;
    object-fit: cover;
    // opacity: 0.4;
    // animation: img 1s linear forwards;

    &:first-child {
      width: 100%;
    }

    &:nth-child(4) {
      opacity: 1;
    }
  }

  // @keyframes img {
  //   100% {
  //     opacity: 1;
  //   }
  // }

  .arrow {
    cursor: pointer;
    display: flex;
    width: 30px;
    height: 30px;
    background-image: url(../../../public/img/arrow.svg);
    background-repeat: no-repeat;
    transform: rotate(90deg);
    margin-left: 20px;

    @media (max-width: 400px) {
      width: 20px;
      height: 20px;
      margin-left: 5px;
    }
  }

  .left {
    transform: rotate(270deg);
    margin-left: 0px;
    margin-right: 20px;

    @media (max-width: 400px) {
      margin-right: 5px;
    }
  }

  @media (max-width: 700px) {
    .arrow {
      display: none;
    }
  }

  @media (max-width: 769px) {
    &-container {
      width: 650px;
      height: 650px;
    }
  }

  @media (max-width: 700px) {
    &-container {
      width: 300px;
      height: 300px;
    }
  }
}

.scroll {
  overflow: hidden;
  padding-right: 14px;
}
