.filter {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #575b5e;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-top: 10px;
  background: #f5f5f5;

  &-load-more {
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    bottom: 20px;
    width: 130px;
    height: 50px;
    margin-right: 20px;
    font-size: 14px;
    border: 2px solid #e8ae39;
    border-radius: 10px
  }

  &-btns {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-left: 20px;
    margin-right: 20px;

    button {
      display: flex;
      position: relative;
      font-size: 20px;
      padding: 0px;
      margin-top: 20px;
      margin-bottom: 20px;
      margin-right: 60px;
      width: 180px;
      height: 130px;
      color: #575b5e;
      border: 2px solid #e8ae39;
      border-radius: 10px;

      &:last-child {
        margin-right: 0px;
      }

      span {
        display: flex;
        position: absolute;
        justify-content: center;
        width: 178px;
        background: #e8ae39;
        border-radius: 0 0 8px 8px;
        bottom: -1px;
        left: -1px;
      }
    }
  }

  .active-filter-btn {
    border-radius: 10px;
      box-shadow: 10px 10px 21px #000000d9,
                  -10px -10px 21px #fff;
  }

  @media (max-width: 894px) {
    button {
      width: 150px;
      height: 100px;
      margin-right: 30px;
      font-size: 17px;

      span {
        width: 148px;
      }
    }
  }

  @media (max-width: 755px) {
    button {
      width: 130px;
      height: 80px;
      margin-right: 20px;
      font-size: 14px;

      span {
        width: 128px;
      }
    }
  }

  @media (max-width: 600px) {
    button {
      width: 100px;
      height: 60px;
      margin-right: 10px;
      font-size: 11px;
      span {
        width: 98px;
      }
    }
  }

  @media (max-width: 420px) {
    button {
      width: 80px;
      height: 50px;
      margin-right: 10px;
      font-size: 9px;
      span {
        width: 78px;
      }
    }
  }
}

.kitchens {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  padding-top: 20px;
  padding-bottom: 80px;
  padding-left: 50px;
  padding-right: 50px;
  justify-content: center;
  background: #f5f5f5;

  &-box {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    justify-content: center;
    margin-right: 20px;
    margin-bottom: 20px;
    width: 370px;
    border: 2px solid #e8ae39;
    border-radius: 10px;

    .btn-img {
      display: none;
      position: absolute;
      top: 40%;
      border-radius: 5px;
      border: none;
      background: #e8ae39;
      // color: #787883;             
    }

    &:hover {
      .btn-img {
        display: flex;
      }
    }

    @media (max-width: 895px) {
      width: 300px;
    }

    
    @media (max-width: 768px) {
      &:hover {
        .btn-img {
          display: none;
        }
      }
    }

    @media (max-width: 755px) {
      width: 250px;
    }

    @media (max-width: 654px) {
      width: 200px;
    }

    @media (max-width: 554px) {
      width: 300px;
      margin-right: 0px;
    }
  }


  &-item {
    width: 100px;
    margin: 10px;
    cursor: pointer;
    
    &:nth-child(4) {
      opacity: 0.2;
    }

    &:first-child {
      width: 350px;
    }

    &-lenght {
      position: absolute;
      font-size: 22px;
      font-style: italic;
      color: #575b5e;
      bottom: 50px;
      right: 50px;

      &::before {
        content: '+';
      }
    }

    @media (max-width: 895px) {
      width: 78px;

      &:first-child {
        width: 280px;
      }

      &-lenght {
        bottom: 40px;
        right: 40px
      }
    }

    @media (max-width: 755px) {
      width: 62px;

      &:first-child {
        width: 230px;
      }

      &-lenght {
        bottom: 20px;
        right: 30px
      }
    }

    @media (max-width: 654px) {
      width: 45px;

      &:first-child {
        width: 180px;
      }

      &-lenght {
        bottom: 10px;
        right: 20px
      }
    }

    @media (max-width: 554px) {
      width: 78px;

      &:first-child {
        width: 280px;
      }

      &-lenght {
        bottom: 40px;
        right: 40px
      }
    }
  }
}

.none {
  display: none;
}

.none-btn {
  padding-bottom: 0px;
}