@import '../app/app.scss';

.app-header {
	display: flex;
	justify-content: center;
	background: linear-gradient(89.81deg, rgba(34,36,37,0.679) 0.16%, rgba(7,13,15,0.679) 49.79%, rgba(19,44,52,0) 98.37%), url("../../../public/img/wx600.webp") no-repeat;
	background-size: cover;
	min-height: 728px;
	margin-bottom: 20px;

	.burger {
		display: none;
		position: absolute;
		width: 28px;
		height: 17px;
		top: 10px;
		right: 10px;
		background-image: url(../../../public/img/burger.svg);
		background-color: transparent;
		background-repeat: no-repeat;
		border: none;
	}

	&-wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 100px;
		margin-top: 20px;
		margin-left: 50px;
		margin-right: 50px;
		background: linear-gradient(180deg, rgba(255,255,255,0.3) 0%, rgba(241,243,235,0.03) 100%);
		position: relative;

		.logo {
			margin-left: 40px;
			margin-right: 10px;

			a {
				display: flex;
				width: 80px;
				height: 80px;
				background-image: url(../../img/panda.png);
				background-repeat: no-repeat;
				background-size: contain;
			}
			
		}

		.header-item {
			display: flex;
			margin-right: 100px;
			font-family: 'Kalam', cursive;
		
			&-social {
				display: flex;
				width: 125px;
				padding-left: 10px;
				padding-right: 10px;
				flex-wrap: wrap;
				align-items: center;
				justify-content: center;
				border-right: 1px solid #fff;
		
				p {
					display: flex;
					margin-bottom: 0px;
					color: #fff;
					font-size: 14px;
					line-height: 12px;
				}
		
				a:nth-child(2) {
						margin-right: 10px;
				}
		
				.social-img {
					width: 30px;
				}
			}
		
			&-work {
				display: flex;
				padding-left: 10px;
				padding-right: 10px;
				border-left: 1px solid #fff;
				border-right: 1px solid #fff;

				.header-btn {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 80px;
					height: 90px;
					color: #fff;
					
					a {
						text-align: center;
						padding: 15px 15px;
						border-radius: 18px;
						margin: 7px 9px;
				
						color: #fff;
						text-decoration: none;
						font-size: 14px;
						line-height: 12px;
				
						&:hover {
							border: 1px solid #e8ae39;
							border-radius: 20%;
						}
				
						img {
							margin-bottom: 6px;
						}
					}
				
					&:first-child {
						margin-right: 20px;
				
						img {
							margin-bottom: 10px;
						}
					}
				}
			}
		}
	}

	@media (max-width: 912px) {
		min-height: 628px;

		.btn-main {
			top: 450px;
			left: 100px;
		}

		&-wrapper {
			.logo {
				margin-left: 30px;
			}

			.header-item {
				margin-right: 10px;

				&-work {
					.header-btn {
						&:first-child {
							margin-right: 0px;
						}
					}
				}
			}
		}
	}

	@media (max-width: 820px) {
		&-wrapper {
			.header-item {
				.work-time {
					margin-left: 20px;
				}
			}
		}
	}

	@media (max-width: 768px) {
		&-wrapper {
			margin-top: 0px;
			margin-left: 0px;
			margin-right: 0px;

			.header-item {
				width: 100%;
				margin-right: 0px;
				justify-content: center;
				transition: all 0.3s;

				&-work {
					margin-left: 30px;
					border: none;
				}

				&-social {
					border: none;
				}
			}

			.logo {
				display: flex;
				position: absolute;
				top: 25px;
				margin-left: 20px;
				z-index: 99;
			}
		}
	}

	@media (max-width: 700px) {
		.burger {
			display: flex;
			top: 15px;
			right: 15px;
			z-index: 98;
		}

		.burger.avtive-burger {
			background-image: url(../../../public/img/close.svg);
			background-position: center;
		}

		.heading {
			top: 300px;
		}

		.btn-main {
			top: 490px;
		}

		&-wrapper {
			background: none;
			align-items: flex-start;
			height: 250px;

			.header-item {
				flex-direction: column;
				align-items: center;
				transform: translateY(-130%);
				background: none;

				&-work {
					margin-left: 0px;
				}

				&-social {
					margin-bottom: 15px;

					p {
						margin-bottom: 5px;
					}
				}

				.work-time {
					margin-left: 0px;
					margin-bottom: 10px;
				}
			}
	
			.header-item.active {
				transform: translateY(0);
				background: linear-gradient(180deg, rgba(64,64,64,0.5) 0%, rgba(64,64,64,0.5) 100%);
			}

			.logo {
				top: 10px;
				margin-left: 10px;
			}
		}
	}

	@media (max-width: 540px) {
		.btn-main {
			left: 50px;
		}
	}

}

.heading {
	display: flex;
	font-family: 'Kalam', cursive, sans-serif;
	position: absolute;
	color: #fff;
	top: 200px;
	margin-left: 20px;
	margin-right: 20px;
}

.work-time {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	align-content: center;
	justify-content: center;
	width: 150px;
	margin-left: 50px;
	color: #fff;

	p {
		display: flex;
		font-size: 14px;
		margin: 0px;
		padding: 0px;
		color: #e8ae39;
	}

	a {
		display: flex;
		text-decoration: none;
		color: #fff;
	}
}

.btn-main {
	display: flex;
	position: absolute;
	align-items: center;
	justify-content: center;
	width: 200px;
	height: 60px;
	top: 500px;
	left: 200px;
	font-size: 24px;
	background:#e8ae39;
	border-radius: 30px;
	border: none;

	&:hover {
		background: #fff;
	}
}

