.advantages-box {
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  background: #f5f5f5;
}

.advantages-wrapper {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 20px 50px 20px 50px;
  padding-top: 35px;
  padding-bottom: 10px;
  border: 2px solid #e8ae39;
  border-radius: 20px;

  &-header {
    display: flex;
    position: absolute;
    font-size: 28px;
    top: 0px
  }

  .advantages-burger {
    display: none;
    position: absolute;
    width: 30px;
    height: 30px;
    padding: 0px;
    margin: 0px;
    right: 10px;
    top: 10px;

    &::after, &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 3px;
      background: gray;
      left: 6px;
      top: 12px;
    }
  
    &::before {
      transform: rotate(-90deg);
    }
  }

  .advantages {
    display: flex;
    width: 300px;
    flex-direction: column;
    margin: 30px;
    border-bottom: 2px solid #e8ae39;

    &-img {
      width: 30px;
      margin-bottom: 10px;
    }

    h3 {
      font-size: 18px;
      font-weight: 800;
      color: #575b5e;
    }

    span {
      color: #575b5e;
    }
  }

  @media (max-width: 912px) {
    .advantages {
      margin: 20px;
    } 
  }

  @media (max-width: 768px) {
    .advantages {
      width: 250px;
    } 
  }

  @media (max-width: 700px) {
    .advantages {
      width: 200px;
    } 
  }

  @media (max-width: 650px) {
    .advantages-burger {
      display: flex;
    }

    .advantages {
      display: none;
      width: 300px;
    }
  }

  .active {
    display: flex;
  }

  .active-burger {
    &::before {
      display: none;
    }
  }

  @media (max-width: 470px) {
    &-header {
      font-size: 22px;
      top: 5px;
    }

    .advantages-burger {
      right: 10px;
      top: 11px;
      width: 20px;
      height: 20px;

      &::after, &::before {
        width: 15px;
        height: 2px;
      }
    }
  }
}

