.faq {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  padding-top: 20px;
  margin-bottom: 20px;
  background: #f5f5f5;

  h2 {
    margin-bottom: 20px;
  }

  &-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 50px;
    margin-bottom: 50px;
  }

  &-item[open] {
    summary::after {
      content: "–";
    }
  }

  &-item {
    display: flex;
    margin-bottom: 20px;
    margin-right: 50px;

    summary {
      display: flex;
      position: relative;
      flex-wrap: wrap;
      align-items: center;
      width: 450px;
      height: 50px;
      font-size:1em;
      font-weight: bold;
      padding-left: 10px;
      cursor: pointer;
      background: #fff;
      

      &::-webkit-details-marker{display:none;}
      &::-moz-list-bullet{list-style-type:none;}
      &::marker{display:none;}

      &::after {
        content: "+";
        position: absolute;
        right: 10px;
      }
    }

    &-text {
      display: flex;
      width: 450px;
      padding: 10px;
      background:#fff;
    }

    @media (max-width: 980px) {
      summary {
        width: 650px;
      }

      &-text {
        width: 650px;
      }
    }

    @media (max-width: 680px) {
      summary {
        width: 450px;
      }

      &-text {
        width: 450px;
      }
    }

    @media (max-width: 565px) {
      summary {
        width: 360px;
        font-size: 0.8em;
        padding-right: 20px;
      }

      &-text {
        width: 360px;
        font-size: 0.8em;
      }
    }

    @media (max-width: 475px) {
      summary {
        width: 315px;
        font-size: 0.8em;
        padding-right: 30px;
      }

      &-text {
        width: 315px;
        font-size: 0.8em;
      }
    }
  }

  @media (max-width: 1166px) {
    &-box {
      margin-left: 20px;
    }

    &-item {
      margin-right: 20px;
    }
  }
}

