.form-wrapper {
  display: flex;
  position: relative;
  justify-content: center;
  flex-wrap: wrap;
  background: #f5f5f5;
  padding-top: 20px;
  padding-bottom: 20px;

  .form {

    &-dialog {
      display: flex;
      width: 700px;
      height: 100%;
    }

    &-content {
      position: relative;
      width: 100%;
      padding: 40px;
      background-color: #fff;
      border: 1px solid rgba(0, 0, 0, .2);
      // max-height: 80vh;
    }

    &-title {
      text-align: center;
      font-size: 22px;
      text-transform: uppercase;
    }

    &-input {
      display: block;
      margin: 30px auto 30px auto;
      width: 380px;
      height: 60px;
      background: #fff;
      box-shadow: 0 4px 15px rgba(0, 0, 0, .2);
      border: none;
      font-size: 18px;
      text-align: center;
      padding: 0 20px;
      outline: 0;
    }

    &-btn {
      display: block;
      width: 280px;
      height: 50px;
      margin: 0 auto;
    }

    @media (max-width: 1120px) {
      .form {
        &-dialog {
          width: 500px;
        }
      }
    }

    @media (max-width: 920px) {
      .form {
        &-dialog {
          width: 400px;
        }
    
        &-input {
          width: 280px;
        }
    
        &-btn {
          width: 200px;
        }
      }
    }

    @media (max-width: 768px) {
      .form {
        &-dialog {
          width: 350px;
        }
      }
    }

    @media (max-width: 670px) {
      .form {
        &-dialog {
          width: 400px;
        }
      }
    }

    @media (max-width: 450px) {
      .form {
        &-dialog {
          width: 350px;
        }
      }
    }
  }

  .contact {
    display: flex;
    flex-direction: column;
    width: 400px;;
    padding: 40px;
    padding-left: 110px;
    color: #fff;
    background-color: #e8ae39;
    // max-height: 80vh;

    &-title {
      display: flex;
      text-align: center;
      font-size: 22px;
      text-transform: uppercase;
    }

    &-adress {
      &::before {
        content: "";
        position: absolute;
        width: 40px;
        height: 40px;
        top: 20px;
        left: -60px;
        background-image: url(../../img/location.svg);
      }
    }

    &-work {
      &::before {
        content: "";
        position: absolute;
        width: 40px;
        height: 40px;
        top: 20px;
        left: -60px;
        background-image: url(../../img/clock.svg);
      }
    }

    &-email {
      &::before {
        content: "";
        position: absolute;
        width: 40px;
        height: 40px;
        top: 20px;
        left: -60px;
        background-image: url(../../img/email.svg);
      }
    }

    &-phone {
      &::before {
        content: "";
        position: absolute;
        width: 40px;
        height: 40px;
        top: 20px;
        left: -60px;
        background-image: url(../../img/phone.svg);
      }
    }

    span {
      display: flex;
      position: relative;
      flex-direction: column;
      font-size: 20px;
      padding: 10px;

      a {
        text-decoration: none;
        color: inherit;
      }

      p {
        padding-bottom: 0px;
        margin-bottom: 0px;
      }
    }

    @media (max-width: 1120px) {
        width: 350px;
    }

    @media (max-width: 768px) {
      width: 300px;

      span {
        font-size: 16px;
        padding: 9px;
      }
    }

    @media (max-width: 670px) {
      width: 400px;

      span {
        font-size: 20px;
        padding: 10px;
      }
    }

    @media (max-width: 450px) {
      width: 350px;
    }  
  }

  // @media (max-width: 1120px) {
  //   .form {
  //     &-dialog {
  //       width: 500px;
  //     }
  //   }
  // }
}

.message-box {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  top: 0px;
  right: 0;
  left: 0px;
  bottom: 0;
  z-index: 9998;
}

.message {
  display: flex;
  align-items: center;
  text-align: center;
  width: 350px;
  height: 200px;
  color: #000;
  box-shadow: 0 4px 15px rgba(0, 0, 0, .3);
  background-color:#f5f5f5;
  p {
    display: flex;
    font-size: 22px;
  }
}