.footer {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;
  height: 100px;
  margin-top: 20px;
  background: black;

  &-logo {
    display: flex;
    width: 80px;
    height: 80px;
    margin-left: 50px;
    background-image: url(../../img/panda.png);
    background-repeat: no-repeat;
    background-size: contain;
  }

  &-contact {
    display: flex;
    position: absolute;
    right: 100px;
  }

  .work-time {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 150px;
    margin-left: 50px;
    color: #fff;
  
    p {
      display: flex;
      font-size: 14px;
      margin: 0px;
      padding: 0px;
      color: #F3CD82;
    }
  
    a {
      display: flex;
      text-decoration: none;
      color: #fff;
    }
  }
  
  .contact-email {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 120px;
    margin-left: 50px;
    color: #F3CD82;

    a {
      text-decoration: none;
      color: #fff;
    }

    p {
      padding-bottom: 0px;
      margin-bottom: 0px;
    }
  }

  .header-item-work {
    display: flex;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;

    .header-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 70px;
      height: 80px;
      color: #fff;
      
      a {
        text-align: center;
        padding: 15px 15px;
        border-radius: 18px;
        color: #fff;
        text-decoration: none;
        font-size: 14px;
        line-height: 12px;
    
        &:hover {
          border: 1px solid #e8ae39;
          border-radius: 20%;
        }
    
        img {
          margin-bottom: 0px;
        }
      }
    
      &:first-child {
        margin-right: 20px;
    
        img {
          margin-bottom: 4px;
        }
      }
    }
  }

  @media (max-width: 768px) {
    &-logo {
      margin-left: 20px;
    }

    &-contact {
      right: 10px;
    }

    .work-time, .contact-email {
      margin-left: 0px;
    }
  }

  @media (max-width: 580px) {
    height: 200px;

    .footer-logo {
      margin-bottom: 80px;
    }

    &-contact {
      flex-direction: column;
      align-items: center;
      right: 80px;
    }

    .work-time {
      margin-top: 5px;
    }
  }
}

.copy {
  display: flex;
  width: 100%;
  margin: 0px;
  padding: 0px;
  justify-content: center;
}
